<template>
    <div id="app" v-if="show">
        <div class="login">
            <img :src="avatar" autofocus="autofocus" alt="avatar">
            <input type="text" placeholder="手机号/会员名" maxlength="10" v-model="username">
            <input type="password" placeholder="请输入密码" maxlength="16" v-model="password">
            <div class="btngroup">
                <router-link to="/codelogin">验证码登陆</router-link>
                <router-link to="/register">免费注册</router-link>
            </div>
            <mt-button type="danger" size="small" @click="login">登陆</mt-button>
        </div>
    </div>
</template>
<script>
import boy from '../assets/boy.png'
import girl from '../assets/girl.png'
import {Toast} from "mint-ui"
export default {
    name:"three",
    props:["show"],
    data(){
        return{
            sex:0,
            avatar:boy,
            username:"",
            password:""
        }
    },
    created(){
        
    },
    methods:{
        login(){
            var username = this.username
            var password = this.password
            if(username.trim().length==0 || password.trim().length==0){
                Toast('账号密码不能为空');
            }else{
                
            }
        }
    },
    computed:{
    }
}
</script>
<style scoped>
.login{
    height: 90vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}
.login img{
    height: 4.375rem;
    width: 4.375rem;
    margin-bottom: .625rem;
}
.mint-button{
    width: 80%;
    border-radius: 1.25rem;
    margin-top: 1.875rem;
}
.login input{
    width: 80%;
    height: 1.875rem;
    border: none;
    border-bottom:red .0625rem solid; 
    margin-top: .9375rem;
    text-indent: .625rem;
}
.btngroup{
    width: 80%;
    display: flex;
    justify-content: space-between;
    font-size: .75rem;
    margin-top: .625rem;
    
}
a{
    color: rgb(20, 20, 20);
}
</style>



