<template>
<div>
<mt-tab-container  class="tabbar" v-model="active">
  <mt-tab-container-item id="猜你喜欢">
    <one />
  </mt-tab-container-item>
  <mt-tab-container-item id="书架">
    <two/>
  </mt-tab-container-item>
  <mt-tab-container-item id="我的">
    <three/>
  </mt-tab-container-item>
</mt-tab-container>
<mt-tabbar v-model="selected" :fixed=true>
  <mt-tab-item id="猜你喜欢" >
    <img slot="icon"  src="../assets/xin.png">
    猜你喜欢
  </mt-tab-item>
  <mt-tab-item id="书架" >
    <img slot="icon" src="../assets/shujia.png">
    书架
  </mt-tab-item>
  <mt-tab-item id="我的">
    <img slot="icon" src="../assets/wode.png">
    我的
  </mt-tab-item>
</mt-tabbar>
</div>
</template>
<script>
import one from '../views/one'
import two from '../views/two'
import three from '../views/three'
export default {
    name:'bottom',
    data:function(){
      return{
        selected:"猜你喜欢",
        active:"猜你喜欢"
      }
    },
    watch:{
      selected:function(e){
        this.active=e
      }
    },
    components:{
      one,two,three
    }
}
</script>
<style scoped>
.tabbar{
  margin-bottom: 3.125rem;
}
</style>



