import axios from 'axios'
axios.defaults.baseURL = "http://www.fi888.cn:5000"
export function index(){
    return new Promise((resolve,reject)=>{
        return axios.get("/").then(res=>resolve(res)).catch(err=>reject(err))
    })
}
export function list(url){
    return new Promise((resolve,reject)=>{
        return axios.get("/list",{params:{ url }}).then(res=>resolve(res)).catch(err=>reject(err))
    })
}
export function getpic(url){
    return new Promise((resolve,reject)=>{
        return axios.get("/getpic",{params:{ url }}).then(res=>resolve(res)).catch(err=>reject(err))
    })
}
export function search(name){
    return new Promise((resolve,reject)=>{
        return axios.get("/search",{params:{ name }}).then(res=>resolve(res)).catch(err=>reject(err))
    })
}
export function tag(){
    return new Promise((resolve,reject)=>{
        return axios.get("/tag").then(res=>resolve(res)).catch(err=>reject(err))
    })
}
export function fl(tag,state,page){
    return new Promise((resolve,reject)=>{
        return axios.get("/fl",{params:{ tag,state,page }}).then(res=>resolve(res)).catch(err=>reject(err))
    })
}


