<template>
    <div id="app">
        <div class="searchbtn">
            <input type="text" autofocus="autofocus"  placeholder="搜索作品、作者名" v-model="name">
            <div class="ss" @click="search(name)">搜索</div>
        </div>
        <div class="total" v-show="total">
            "{{ searchname }}"的搜索结果
        </div>
        <div v-if="imgUrl.length>0">
            <div class="list" v-for="(value,index) in imgUrl.length%4==0 ? imgUrl.length/4 : parseInt(imgUrl.length/4)+1"  :key="index">
                <div v-for="(v,i) in (imgUrl.length-4*value>0?4:Math.abs(index*4-imgUrl.length))"  :key="i">
                    <router-link :to="{path:'list',query:{ url:url[4*index+i] }}"><singleimg :src="imgUrl[4*index+i]" :tt1="title[4*index+i]" :tt2="nickName[4*index+i]"/></router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { search } from "../api/index"
import singleimg from "../components/singleimg"
export default {
    name:"search",
    data(){
        return{
            name:"",
            total:"",
            imgUrl:[],
            total:[],
            nickName:[],
            title:[],
            url:[],
            searchname:"",
            total:false
        }
    },
    methods:{
        search(name){
            if(name)
                search(name).then((res)=>{
                    let data = res.data
                    if(!data.error)
                        this.total = data.imgUrl.length
                        this.imgUrl = data.imgUrl
                        this.nickName = data.nickName
                        this.title = data.title
                        this.url = data.url
                        this.searchname = name
                        this.total = true
                })
        },
    },
    components:{
        singleimg
    }
}
</script>
<style scoped>
#app{
    width: 100%
}
.searchbtn{
    width: 100%;
    height: 3.125rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.searchbtn input{
    height: 1.875rem;
    border: none;
    width: 65%;
    margin-left: 10%;
    border-radius: 1.875rem;
    padding-left:5%; 
    padding-right: 5%;
    font-size: .875rem;
    background: rgb(240, 240, 240);
}
.ss{
    color: black;
    font-size: .875rem;
}
.total{
    text-indent: .625rem;
    font-weight: bold;
}
.list{
    display: flex;
    margin-top: .625rem;
    justify-content:space-around;
}


</style>



