<template>
    <div id="app" v-if="mask">
        
        <div class="header">
            <img v-lazy="imgurl" alt="img">
            <div class="tt">
                <span class="tt1" v-text="title"></span>
                <span class="tt2" v-text="nickname"></span> 
            <div class="btngroup">
               <router-link :to="{ path:'pic',query:{url:listUrl[listUrl.length-1]}}"><button class="btn1">查看第一话</button></router-link>
               <button class="btn2">收藏</button>
            </div>
            </div>
        </div>
        <div class="content">
            <div v-for="(v,i) in listTitle" :key="i">
                <div class="list" @click="listitem(listUrl[i])">{{ v }}<img v-if="listUrl[i].indexOf('javascript:void(0)')!=-1" class="suo" src="../assets/suo.png"/></div>
            </div>
        </div>
    </div>
</template>

<script>
import {list} from "../api/index.js"
import {Indicator} from 'mint-ui'
import { MessageBox } from 'mint-ui';
export default {
    name:"list",
    data(){
        return {
            listTitle:[],
            listUrl:[],
            listcontent:"",
            title:"",
            imgurl:"",
            nickname:"",
            mask:false
        }
    },
    beforeCreate(){
        Indicator.open({
        text: 'Loading...',
        spinnerType: 'double-bounce'
        });
    },
    created(){
        var url= this.$route.query.url
        this.list(url)
    },
    methods:{
        list(url){
            list(url).then((res)=>{
                
                Indicator.close()
                if(!res.data.error){
                    let data = res.data
                    this.listTitle = data.listTitle
                    this.listUrl = data.listUrl
                    this.listcontent = data.listcontent
                    this.title = data.title
                    this.nickname = data.nickname
                    this.imgurl = data.imgurl
                    this.mask = true
                }else{
                    MessageBox.alert('出错了！').then(action => {  
                        this.mask = true
                        this.$router.go(-1)
                    });
                }
            })
        },
        listitem(url){
            if(url.indexOf("javascript:void(0)")==-1){
                this.$router.push({
                    path:"pic",
                    query:{
                        url
                    }
                })
            }
        }
    }
    
}
</script>
<style scoped>
/* 338 213 */
#app{
    margin-left: .625rem;
    margin-right: .625rem;
}
.btngroup{
    display: flex;
}
.btngroup button{
    width: 4.375rem;
    font-size: .625rem;
    height: 1.5625rem;
    line-height: 1.5625rem;
    border-radius:.625rem;
    color: gray;
}
.header{
    height: 4.4375rem;
    display: flex;
    border-left:rgb(230, 230, 230) solid .0625rem;
    border-right:rgb(230, 230, 230) solid .0625rem;
    padding: 1.875rem 1.25rem;
}
.header img{
    width: 112px;
    border-radius: .625rem;
}
.content{
    margin-left: 1.25rem;
    margin-right: 1.25rem;
}

.list{
    text-align: center;
    border-top: .0625rem solid rgb(230, 230, 230);
    padding: .625rem 0;
    font-size: .875rem;
    color: gray;
}
.tt{
    font-size: .8125rem;
    display: flex;
  	flex-direction: column;
    line-height: 1.25rem;
    margin-left:.625rem; 
    justify-content: space-around;
}
.tt2{
    color: lightgray;
}
.btn1{
    background: yellow;
    border: none;
}
.btn2{
    background: white;
    border:solid gray .0625rem;
    margin-left: .625rem;
}
.suo{
    width: .625rem;
    height: .625rem;
    margin-left: 1.25rem;
}
</style>



