<template>
    <div @click="all">
        <img v-for="v in img_url" v-lazy="v" alt="img" :key="v">
        <div class="btngrup" v-show="btn">
            <mt-button @click="up()" :disabled="up_url.indexOf('javascript:void(0)')!=-1" class="button" type="primary">
                上一话
            </mt-button>
            <mt-button @click="next()" :disabled="next_url.indexOf('javascript:void(0)')!=-1"  class="button" type="primary">
                下一话
            </mt-button>
        </div>
    </div>
</template>
<script>
import { getpic } from "../api/index"
export default {
    name:"pic",
    data(){
        return{
            img_url:[],
            url:"",
            next_url:"",
            up_url:"",
            btn:false
        }
    },
    created(){
        var url = this.$route.query.url
        this.url = url
        this.index(this.url)
    },
    methods:{
        index(url){
            getpic(url).then((res)=>{
                let data = res.data
                this.img_url = data.img_url
                this.next_url = data.next_url
                this.up_url = data.up_url
                this.btn = true
            })
        },
        up(){
            this.$router.push({path:"/pic",query:{ url:this.up_url }})
        },
        next(){
            this.$router.push({path:"/pic",query:{ url:this.next_url }})
        },
        all(){

        }
    },
    computed:{
     },

}
</script>
<style scoped>
img{
    width: 100%;
}
div{
    font-size: 0;
}
.btngrup{
    display: flex;
    justify-content: space-around;
    margin-top: .625rem;
    margin-bottom: .625rem;
}
.btngrup .button{
    border: none;
    font-size: 14px;
    height: 1.875rem;
    width: 5rem;
    border-radius: 1.25rem;
}
.tobottom{
    position: fixed;
    width: 6.25rem;
    height: 6.25rem;
    top: 6.25rem;
    background-color: red;
}
</style>



