import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import pic from '@/views/pic'
import list from '@/views/list'
import fl from '@/views/fl'
import search from '@/views/search'
import register from '@/views/register'
import codelogin from '@/views/codelogin'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/myp/',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/pic',
      name: 'pic',
      component: pic
    },
    {
      path: '/list',
      name: 'list',
      component: list
    },
    {
      path: '/fl',
      name: 'fl',
      component: fl
      
    },
    {
      path: '/search',
      name: 'search',
      component: search
      
    },
    {
      path: '/codelogin',
      name: 'codelogin',
      component: codelogin
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
  ]
})



